'use client';

import { useState, useEffect } from 'react';
import { Folder, File, ChevronRight, ChevronDown } from 'lucide-react';

interface FileNode {
  name: string;
  type: 'file' | 'directory';
  children?: FileNode[];
  path: string;
}

interface FileExplorerProps {
  onFileSelect: (path: string) => void;
}

export function FileExplorer({ onFileSelect }: FileExplorerProps) {
  const [files, setFiles] = useState<FileNode[]>([]);
  const [expandedFolders, setExpandedFolders] = useState<Set<string>>(new Set());

  useEffect(() => {
    // Load projects and files
    const loadFiles = async () => {
      try {
        const response = await fetch('/api/files');
        const data = await response.json();
        setFiles(data);
      } catch (error) {
        console.error('Failed to load files:', error);
      }
    };
    loadFiles();
  }, []);

  const toggleFolder = (path: string) => {
    setExpandedFolders(prev => {
      const next = new Set(prev);
      if (next.has(path)) {
        next.delete(path);
      } else {
        next.add(path);
      }
      return next;
    });
  };

  const renderNode = (node: FileNode, level: number = 0) => {
    const isExpanded = expandedFolders.has(node.path);
    const paddingLeft = `${level * 16}px`;

    return (
      <div key={node.path}>
        <div
          className="flex items-center py-1 px-2 hover:bg-[#2d2d2d] cursor-pointer text-sm"
          style={{ paddingLeft }}
          onClick={() => {
            if (node.type === 'directory') {
              toggleFolder(node.path);
            } else {
              onFileSelect(node.path);
            }
          }}
        >
          {node.type === 'directory' && (
            <span className="mr-1">
              {isExpanded ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
            </span>
          )}
          {node.type === 'directory' ? (
            <Folder size={16} className="mr-2 text-[#dcb67a]" />
          ) : (
            <File size={16} className="mr-2 text-[#8a9199]" />
          )}
          <span className="truncate">{node.name}</span>
        </div>
        {node.type === 'directory' && isExpanded && node.children?.map(child => renderNode(child, level + 1))}
      </div>
    );
  };

  return (
    <div className="h-full bg-[#1e1e1e] border-r border-[#424242] w-64 overflow-y-auto">
      <div className="p-2 text-xs font-medium text-[#8a9199] uppercase">Explorer</div>
      <div className="text-[#d4d4d4]">
        {files.map(node => renderNode(node))}
      </div>
    </div>
  );
}
