'use client';
import { useChat } from '@ai-sdk/react';
import { useState } from 'react';
import { FileExplorer } from '@/components/FileExplorer';

export default function Chat() {
  const [autoMode, setAutoMode] = useState(false);
  const [selectedFile, setSelectedFile] = useState<string | null>(null);
  const { messages, input, handleInputChange, handleSubmit } = useChat({
    body: {
      autoMode: autoMode,
      currentFile: selectedFile
    }
  });

  const handleFileSelect = (path: string) => {
    setSelectedFile(path);
  };

  return (
    <div className="min-h-screen bg-[#1e1e1e] text-[#d4d4d4] flex flex-col">
      {/* VSCode-style header */}
      <header className="bg-[#323233] border-b border-[#424242] px-4 py-2">
        <div className="flex items-center justify-between">
          <h1 className="text-sm font-medium">iLL Coder by iLL Ai</h1>
          <div className="flex items-center space-x-2">
            <label className="flex items-center space-x-2 text-sm">
              <span>Auto Mode</span>
              <div className="relative inline-block w-10 h-5">
                <input
                  type="checkbox"
                  name="autoMode"
                  checked={autoMode}
                  onChange={(e) => setAutoMode(e.target.checked)}
                  className="sr-only peer"
                  value="true"
                />
                <div className="w-10 h-5 bg-[#424242] rounded-full peer peer-checked:after:translate-x-full 
                              after:content-[''] after:absolute after:top-0.5 after:left-0.5 after:bg-[#d4d4d4] 
                              after:rounded-full after:h-4 after:w-4 after:transition-all peer-checked:bg-[#007acc]">
                </div>
              </div>
            </label>
          </div>
        </div>
      </header>

      {/* Main content area with file explorer and chat */}
      <div className="flex-1 flex">
        {/* File Explorer */}
        <FileExplorer onFileSelect={handleFileSelect} />

        {/* Chat Area */}
        <div className="flex-1 flex flex-col">
          {/* Selected file indicator */}
          {selectedFile && (
            <div className="bg-[#2d2d2d] px-4 py-2 text-sm border-b border-[#424242]">
              Current file: {selectedFile}
            </div>
          )}

          {/* Messages */}
          <div className="flex-1 overflow-auto p-4 space-y-4">
            {messages.map(m => (
              <div key={m.id} className={`flex ${m.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                <div className={`max-w-[85%] rounded ${
                  m.role === 'user' 
                    ? 'bg-[#2d2d2d] text-[#d4d4d4]' 
                    : 'bg-[#1e1e1e] border border-[#424242]'
                  }`}>
                  {/* Role indicator */}
                  <div className="px-3 py-1 text-xs border-b border-[#424242]">
                    {m.role === 'user' ? 'You' : 'AI Assistant'}
                  </div>
                  
                  {/* Message content */}
                  <div className="px-3 py-2 whitespace-pre-wrap">
                    {m.content.split('```').map((part, i) => {
                      if (i % 2 === 0) {
                        return <span key={i}>{part}</span>;
                      } else {
                        const [lang, ...code] = part.split('\n');
                        return (
                          <div key={i} className="my-2">
                            {/* Code block header */}
                            <div className="bg-[#2d2d2d] px-3 py-1 text-xs border border-[#424242] rounded-t flex justify-between items-center">
                              <span>{lang || 'plaintext'}</span>
                              {autoMode && <span className="text-[#007acc]">Auto-executing...</span>}
                            </div>
                            {/* Code content */}
                            <pre className="bg-[#1e1e1e] p-3 rounded-b border border-t-0 border-[#424242] overflow-x-auto">
                              <code className="text-sm font-mono">{code.join('\n')}</code>
                            </pre>
                          </div>
                        );
                      }
                    })}
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Input area */}
          <div className="border-t border-[#424242] bg-[#1e1e1e] p-4">
            <form onSubmit={handleSubmit} className="flex gap-2">
              <input
                type="text"
                value={input}
                onChange={handleInputChange}
                placeholder={autoMode ? "I'll help you write, run, and fix code automatically..." : "Ask me to solve something with code..."}
                className="flex-1 bg-[#2d2d2d] text-[#d4d4d4] rounded px-3 py-2 
                         border border-[#424242] focus:outline-none focus:border-[#007acc]
                         placeholder-[#808080]"
              />
              <button 
                type="submit" 
                className="bg-[#007acc] text-white px-4 py-2 rounded hover:bg-[#005c99]
                         transition-colors duration-200"
              >
                Send
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
